import React from 'react'
import { Container, Jumbotron } from 'reactstrap'

const Home = () => {
    return (
        <div>
            <Jumbotron fluid className="text-center mt-5">
                <Container fluid>
                <h1>Welcome to the HomePage</h1>
                </Container>
            </Jumbotron>
        </div>
    )
}

export default Home
