import React from 'react'
import { Container, Jumbotron } from 'reactstrap'

const PageNotFound = () => {
    return (
        <div>
            <Jumbotron  fluid className="text-center mt-5">
            <Container fluid>
            <h1 className="display-3">404 Page Not Found</h1>
            </Container>
            </Jumbotron>
        </div>
    )
}

export default PageNotFound
